<template>
    <header :class="{'bgw': getShow, 'border-in' : borderIn }">
        <div class="container top-holder">
            <a href="#" @click.prevent="goTo('Home')" class="pull-left logo" :class="{ 'hide' : getShow }">
                <img alt="Logo" :src="require('@/assets/logo.svg')" height="45px">
            </a>
       <nav class="pull-right">
           <button id="hamburger_menu" @click="$store.commit('toggleMenu')" :class="{'collapse': getShow }">
               <div></div>
               <div></div>
           </button>
           <ul id="nav_menu">
               <nav-list v-for="menu in getMenus" :key="menu.id" v-bind:menu="menu" />
           </ul>
        </nav>  
        </div>  
    </header>
</template>
<script>
import NavList from './NavList.vue'
import { mapGetters } from 'vuex'

export default {
    name: 'Header',
    computed: mapGetters(['getMenus', 'getShow']),
    components: { NavList },
    data() {
        return {
            borderIn: false
        }
    },
    created() {
        document.addEventListener('scroll', this.test)
    },
    destroyed() {
        document.removeEventListener('scroll', this.test)
    },
    methods: {
        goTo(url) {
            this.$store.commit('undoTransform')
            setTimeout(() => {
                this.$router.push({ name: url})
            }, 500);
            setTimeout(() => {
                this.$store.commit('doTransform')
            }, 500);
        },
        test() {
            if (document.documentElement.scrollTop > 440) {
                this.borderIn = true
            } else {
                this.borderIn = false
            }
        }
    }
    
}
</script>

<style scoped>
header{ 
    width: 100%; 
    background-color: hsla(0,0%,100%,.7); 
    height: 80px;
    position: fixed;
    top: 0;
    z-index: 100;
    backdrop-filter: saturate(180%) blur(20px);
}
.bgw{
    background-color: rgba(255, 255, 255, 1) !important; 
}

.top-holder{
    display: table;
    clear: both;
}

nav {
    line-height: 80px;
}
nav ul{
    margin: 0;
}
.logo{
    padding-top: 20px;
    transition: 0.3s all;
}
.hide{
    opacity: 0;
}
#nav_menu{
  display: none;
  margin-right: -20px;
}
#hamburger_menu{
    border: none;
    background-color: transparent;
    padding: 15px 10px;
    cursor: pointer;
    position: absolute;
    top: 25px;
    right: 5px;
    width: 50px;
    height: 40px;
}
#hamburger_menu div{
    width: 25px;
    border-bottom: 1.5px solid;
    color: #2C4C65;
    display: block;
    margin-right: 0;
    transition: 0.5s all cubic-bezier(0.7, 0.11, 0.2, 1) 0s;
    position: absolute;
}
#hamburger_menu div:first-child{
    transform: rotate(0deg) translate(0px,-5px);
}
#hamburger_menu div:nth-child(2){
    transform: rotate(0deg) translate(0px,5px);
}
.collapse div:first-child{
    transform: rotate(-45deg) translate(0px,0px) !important;
}
.collapse div:nth-child(2){
        transform: rotate(45deg) translate(0px,0px) !important;
}
.border-in{
    border-bottom: 1px solid rgba(193, 201, 204, 0.3);
}




</style>
