<template>
    <li>
        <span @mouseup="$store.commit('toggleMenu')">
            <a href="#" v-if="!menu.ext" @click.prevent="goTo(menu.url)" :class="{ 'is-active': menu.active }">{{ menu.name }}</a>
            <a v-if="menu.ext" :href="menu.url" target="_blank">{{ menu.name }}</a>
        </span>
    </li>
</template>
<script>

import { mapGetters } from 'vuex'

export default {
    name: 'MobNavList',
    props: ['menu'],
    computed: mapGetters(['getShow']),
    methods: {
        goTo(url) {
            this.$store.commit('undoTransform')
            setTimeout(() => {
                this.$router.push(url)
            }, 500);
            setTimeout(() => {
                this.$store.commit('doTransform')
            }, 500);
            
        }
    }
}
</script>

<style scoped>
ul li{
    padding: 15px 0;
    list-style-type: none;
}
ul li a{
    color: #2C4C65;
    font-size: 24px;
    display: inline-block;
    text-decoration: none;
    padding: 20px 0;
    font-weight: 400;
}
li a:active{
    color: #0e84e2;
}
.is-active{
    border-bottom: 1px #ED6817 solid;
}

</style>
