<template>
    <section id="home_section">
        <div class="container transform" :class="{ 'transform-in' : getTransform }">
            <h1>My Work</h1>
            <h3>Case Studies</h3>
            <div class="row">
                <thumb-items v-for="thumbnail in thumbnails" :key="thumbnail.id" v-bind:thumbnail="thumbnail" />
            </div>
            <div id="scroll_down">
                <span v-show="!showArrow">
                    <svg xmlns="http://www.w3.org/2000/svg" width="30.396" height="15.239" viewBox="0 0 30.396 15.239">
                        <path d="M5854.545-1048.761l-14.88-14.123a.647.647,0,0,1-.023-.914.647.647,0,0,1,.915-.024l14,13.285,14.22-13.288a.646.646,0,0,1,.914.031.646.646,0,0,1-.031.914Z" transform="translate(-5839.464 1064)" fill="#C1C9CC"/>
                    </svg>

                </span>  
            </div>
            <hr>
            <h3>Social Media Shots</h3>
            <div class="row" id="social_section">
                <shots v-for="shot in shots" :key="shot.id" v-bind:shot="shot">

                </shots>
            </div>
        </div>
    </section>
</template>

<script>
import ThumbItems from '../includes/ThumbItems.vue'
import { mapGetters } from 'vuex'
import Shots from '../includes/Shots.vue'

export default {
    name: 'Home',
    components: { ThumbItems, Shots },
    computed: mapGetters(['getTransform']),
    data() {
        return {
            thumbnails: [
                { id: 3, caption: 'Case study (Designing for social good):', title: 'Phoenix Career Mentorship Service', img: 'thumbnail-33.png', url: 'CaseThree'},
                { id: 2, caption: 'Case study:', title: 'Design Tutorials Website', img: 'thumbnail-22.png', url: 'CaseTwo'},
                { id: 1, caption: 'case study:', title: 'Infinity Movie Trailer', img: 'thumbnail-11.png', url: 'CaseOne'}
            ],
            shots: [
                {id: 1, caption: 'shots:', title: 'Behance', img: 'behance_shots.jpg', url: 'https://www.behance.net/benjaminayimah'},
                {id: 2, caption: 'shots:', title: 'Instagram', img: 'instagram-shots.jpg', url: 'https://www.instagram.com/ben.tekk/'}
            ],
            showArrow: false
            
        }
    },
    created() {
        window.scrollTo(0,0)
        this.$store.commit('setActiveMenu', this.$route.path)
        this.$store.commit('doTransform')
        document.addEventListener('scroll', this.showScroll )
    },
    destroyed() {
        document.removeEventListener('scroll', this.showScroll )
    },
    methods: {
        showScroll() {
            let divPosition = document.getElementById('social_section').offsetTop
            let windowHeight = window.innerHeight
            if(divPosition <= parseInt(window.pageYOffset + windowHeight -177)){
                return(this.showArrow = true)
                //return(console.log('show arrow'))
            }
            else{
                //return(console.log('hide arrow'))
                return(this.showArrow = false)
            }
        }
    }
}
</script>

