<template>
    <li>
        <a v-if="!menu.ext" href="#" @click.prevent="goTo(menu.url)" :class="{ 'is-active': menu.active }">{{ menu.name }}</a>
        <a v-if="menu.ext" :href="menu.url" target="_blank">{{ menu.name }}</a>
    </li>
</template>
<script>

export default {
    name: 'NavList',
    props: ['menu'],
    methods: {
        goTo(url) {
            this.$store.commit('undoTransform')
            setTimeout(() => {
                this.$router.push(url)
            }, 500);
            setTimeout(() => {
                this.$store.commit('doTransform')
            }, 500); 
        }
    }
}
</script>

<style scoped>
li{
    list-style-type: none;
    padding: 0 20px;
}
li a{
    color:#2c4c65;
    text-decoration: none;
    padding: 5px 0;
    font-size: 18px;
    font-weight: 400;
}
li:hover a{
    color: #547793;
}
li a:active{
    color: #0e84e2;
}
.is-active{
    border-bottom: 1px #ED6817 solid;
}
</style>
