<template>
    <div class="col-4">
    <div @mouseover="mouseIn" @mouseout="mouseOut" class="relative thumbnail-holder" :class="{ 'zoom-out' : zoom }">
        <a href="#" @click.prevent="goTo(thumbnail.url)">
            <img class="thumbnails" :src="require('@/assets/'+thumbnail.img)" :alt="thumbnail.title">
            <div v-bind:class="{ 'visible': hover}" class="absolute thumbnail-overlay">
                <div class="caption">
                    <div class="caption-header">{{ thumbnail.caption | uppercase }}</div>
                    <div class="title">{{ thumbnail.title }}</div>
                </div>
            </div> 
        </a>
         
    </div>
    </div>
</template>
<script>

export default {
    name: 'ThumbnailItem',
    props: ['thumbnail'],
    data() {
        return {
            hover: false,
            zoom: false
        }
    },
    methods: {
        mouseIn() {
            this.hover = true
            this.zoom = true
        },
        mouseOut() {
            this.hover = false
            this.zoom = false
        },
        goTo(url) {
            this.$store.commit('undoTransform')
            setTimeout(() => {
                this.$router.push({ name: url})
            }, 500);
        }
    }
    
}
</script>

<style scoped>
.thumbnail-holder{
    transition: 0.3s all;
}
.thumbnails{
    width: 100%;
    border-radius: 14px;
    display: block;
}
.zoom-out{
    transform: scale(0.95);
}

.thumbnail-overlay{
    width: 100%;
    height: 100%;
    background-color: rgb(44, 76, 101, 0.7);
    bottom: 0;
    border-radius: 14px;
    opacity: 0;
    visibility: hidden;
    transition: 0.6s;
}
.visible{
    visibility: visible;
    opacity: 1;
}
.caption{
    padding: 0 40px;
    top: 50%;
    position: absolute;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
}
.caption div{
    color: #ffffff;
}
.caption-header{
   font-size: 20px; 
}
.title{
    font-size: 40px;
    font-weight: 600;
    padding: 15px 0;
    line-height: 40px;
}
</style>
