import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import store from './store'

Vue.filter('capitalize', function(value) {
  if(!value) return ''
  value = value.toString()
  return value.charAt(0).toUpperCase() + value.slice(1)
})
Vue.filter('uppercase', function(value) {
  if(!value) return ''
  return value.toString().toUpperCase();
})



Vue.config.productionTip = false
Vue.use(VueRouter);
import Home from '@/components/views/Home'
import CaseOne from '@/components/views/CaseStudy1'
import CaseTwo from '@/components/views/CaseStudy2'
import CaseThree from '@/components/views/CaseStudy3'
import About from '@/components/views/About'
import './registerServiceWorker'

const routes = [
  { path: '/', component: Home , name: 'Home'},
  { path: '/case-study-1', component: CaseOne , name: 'CaseOne'},
  { path: '/case-study-2', component: CaseTwo , name: 'CaseTwo'},
  { path: '/case-study-3', component: CaseThree , name: 'CaseThree'},
  { path: '/about', component: About , name: 'About'},

]

const router = new VueRouter({
  routes,
  mode: 'history'
})

new Vue({
  render: h => h(App),
  router,
  store
}).$mount('#app')
