export default {
    state: {
        menus: [
            { id: 1, name: 'Work', url: '/', active: false, ext: false },
            { id: 2, name: 'About', url: '/about', active: false, ext: false },
            { id: 3, name: 'Resume', url: 'https://docs.google.com/document/d/1qKlos72NGqJiJzPnZopj777ASBBkSrBcbG_M-El_m3Y/edit?usp=sharing', active: false, ext: true}
        ],
        logo: '@/assests/logo.png',
        showMenu: false,
        transform: false
    },
    mutations: {
        setActiveMenu: (state, payload) => { 
            state.menus.forEach(element => {
                element.active = false
                if (element.url === payload) {
                    element.active = true
                }
            });
        },
        toggleMenu: (state) => {
            state.showMenu = !state.showMenu
        },
        doTransform: (state) => {
            setTimeout(()=> {
                state.transform = true
            }, 100)
        },
        undoTransform: (state) => {
            state.transform = false
        }
    },
    actions: {

    },
    getters: {
        getMenus(state){
            return state.menus
        },
        getShow(state) {
            return state.showMenu
        },
        getTransform(state) {
            return state.transform
        }
    }
}
